import React from "react";
import Calendar from "../Calendar/Calendar";
import SearchCityBar from "../SearchCityBar/SearchCityBar";
import styles from "./SearchBar.module.css";
import Button from "../Button/Button";
import axios from "axios";

function SearchBar(props) {
  const handlerOnClick = () => {
    const inputCity = document.querySelector("#input_search_city");

    axios
      .get("http://localhost:8080/cars/filter?city=" + inputCity.value)
      .then((response) => {
        console.log(response.data);
        props.handlerSetCars();
        props.handlerSetCars(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div className={styles.container} data-testid="searchBarTestId">
      <h1 className={styles.title}>
        Buscá las mejores ofertas en vehículos de alquiler
      </h1>
      <form className={styles.container_search}>
        <div className={styles.city_picker}>
          <SearchCityBar />
        </div>
        <div className={styles.day_picker}>
          <Calendar />
        </div>
        <div className={styles.btn_container}>
          <Button
            title="Buscar"
            link="/"
            btnStyle="btn--full"
            btnSize="btn--responsive"
            onClick={handlerOnClick}
          />
        </div>
      </form>
      <div className={styles.image}></div>
    </div>
  );
}

export default SearchBar;
