import React, { useEffect, useState } from "react";
import CardCategory from "../CardCategory/CardCategory";
import styles from "./SectionCategory.module.css";
import suv from "../../img/SUV.png";
import descapotable from "../../img/descapotable.jpg";
import limusina from "../../img/limusina.jpg";
import sedan from "../../img/sedan.jpg";
import axios from "axios";

function SectionCategory(props) {
  const [categories, setCategories] = useState([]); //para api
  // const categories = [//para json
  //   {
  //     id: 1,
  //     title: "SUV",
  //     imgUrl: suv,
  //     description: "Mas de 20 modelos",
  //   },
  //   {
  //     id: 2,
  //     title: "Descapotable",
  //     imgUrl: descapotable,
  //     description: "Mas de 10 modelos",
  //   },
  //   {
  //     id: 3,
  //     title: "Luxury",
  //     imgUrl: limusina,
  //     description: "Mas de 10 modelos",
  //   },
  //   {
  //     id: 4,
  //     title: "Sedan",
  //     imgUrl: sedan,
  //     description: "Mas de 10 modelos",
  //   },
  // ];

  const cards = categories.map((category) => (
    <CardCategory
      id={category.id}
      title={category.title}
      img={category.imgUrl}
      description={category.description}
      handlerSetCars={props.handlerSetCars}
    />
  ));

  useEffect(() => {
    //para api
    axios
      .get("http://localhost:8080/categories")
      .then((response) => {
        console.log("categorias", response.data);
        setCategories(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <section className={styles.section}>
      <div className={styles.container}>
        <h2 className={styles.title}>Buscá por tipo de vehiculo</h2>
        <p className={styles.text}>
          Contamos con gran variedad de vehículos para todas sus necesidades.
        </p>
        <div className={styles.cards}>{cards}</div>
      </div>
    </section>
  );
}

export default SectionCategory;
