import { React, useState } from "react";
import "react-datepicker/dist/react-datepicker.css";
import "./CalendarReservation.css";
import DatePicker, { registerLocale } from "react-datepicker";
import es from "date-fns/locale/es";

registerLocale("es", es);

function CalendarReservation(props) {
  const typeOfCalendar = props.type;
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const datesExcluded = props.datesExcluded;
  // const [excludeDated, setExcludeDated] = useState([
  //   new Date(2021, 10, 18),
  //   new Date(2021, 10, 19),
  //   new Date(2021, 10, 20),
  //   new Date(2021, 10, 21),
  //   new Date(2021, 10, 22),
  //   new Date(2021, 10, 23),
  //   new Date(2021, 11, 19),
  //   new Date(2021, 11, 20),
  //   new Date(2021, 11, 21),
  //   new Date(2021, 11, 22),
  //   new Date(2021, 11, 23),
  // ]);

  const getDates = () => {
    if (datesExcluded)
      return datesExcluded.map((dateString) => {
        let date = new Date(dateString);
        date.setHours(date.getHours() + 3);
        return date;
      });
    else return [];
  };

  return (
    <>
      {typeOfCalendar === "calendar--picker" ? (
        <>
          <div className="datePicker_reservation">
            <div className="datePicker_one_month_reservation">
              <DatePicker
                typeOfCalendar={typeOfCalendar}
                dateFormat="dd/MM/yyyy"
                minDate={new Date()}
                locale="es"
                required
                inline
                excludeDates={getDates()}
                selected={(startDate, startDate)}
                selectsRange
                startDate={startDate}
                endDate={endDate}
                monthsShown={1}
                onChange={(date) => {
                  const [start, end] = date;
                  setStartDate(start);
                  setEndDate(end);
                  props.handleDateReservation(start, end);
                }}
              />
            </div>
          </div>
          <div className="datePicker_reservation">
            <div className="datePicker_two_month_reservation">
              <DatePicker
                dateFormat="dd/MM/yyyy"
                minDate={new Date()}
                selected={(startDate, startDate)}
                selectsRange
                startDate={startDate}
                endDate={endDate}
                excludeDates={getDates()}
                locale="es"
                required
                inline
                monthsShown={2}
                onChange={(date) => {
                  const [start, end] = date;
                  setStartDate(start);
                  setEndDate(end);
                  props.handleDateReservation(start, end);
                }}
              />
            </div>
          </div>
        </>
      ) : (
        <>
          <div className="datePicker_reservation">
            <div className="datePicker_one_month_reservation">
              <DatePicker
                dateFormat="dd/MM/yyyy"
                minDate={new Date()}
                locale="es"
                inline
                excludeDates={getDates()}
                monthsShown={1}
              />
            </div>
          </div>
          <div className="datePicker_reservation">
            <div className="datePicker_two_month_reservation">
              <DatePicker
                dateFormat="dd/MM/yyyy"
                minDate={new Date()}
                excludeDates={getDates()}
                locale="es"
                inline
                monthsShown={2}
              />
            </div>
          </div>
        </>
      )}
    </>
  );
}

export default CalendarReservation;
