import React from "react";
import "./Button.css";
import { Link } from "react-router-dom";

function Button({ title, link, type, onClick, btnSize, btnStyle }) {
  const SIZES = ["btn--medium", "btn--large", "btn--responsive"];
  const STYLES = ["btn--full", "btn--void"];

  const buttonStyle = STYLES.includes(btnStyle) ? btnStyle : STYLES[0];
  const buttonSize = SIZES.includes(btnSize) ? btnSize : SIZES[0];

  return (
    <Link to={link}>
      <button
        className={`btn ${buttonStyle} ${buttonSize}`}
        type={type}
        onClick={onClick}
      >
        {title}
      </button>
    </Link>
  );
}

export default Button;
