import React, { useState, useEffect } from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import Home from "./pages/Home/Home";
import Login from "./pages/Login/Login";
import Register from "./pages/Register";
import ProductDetails from "./pages/ProductDetails/ProductDetails";
import Reservation from "./pages/Reservation/Reservation";
import SuccesReservation from "./pages/SuccesReservation/SuccesReservation";

function App() {
  const [user, setUser] = useState({
    name: "",
    lastName: "",
    email: "",
    token: "",
  });
  const [renderHeader, setRenderHeader] = useState(1);

  useEffect(() => {
    let userLocalStorage = localStorage.getItem("user");

    if (userLocalStorage) setUser(JSON.parse(userLocalStorage));
  }, []);

  const changeUser = (newUser) => {
    setUser(newUser);
    localStorage.setItem("user", JSON.stringify(newUser));
  };

  const renderHeaderToggle = () => {
    setRenderHeader(renderHeader + 1);
    console.log(renderHeader);
  };

  return (
    <div className="App">
      <BrowserRouter>
        <Route path="/">
          <Header
            user={user}
            changeUser={changeUser}
            renderHeader={renderHeader}
          />
        </Route>
        <Switch>
          <Route exact path="/">
            <Home renderHeader={renderHeaderToggle} />
          </Route>
          <Route path="/login">
            {user.name === "" ? (
              <Login
                changeUser={changeUser}
                renderHeader={renderHeaderToggle}
              />
            ) : (
              <Redirect to="/" />
            )}
          </Route>
          <Route path="/register">
            {user.name === "" ? (
              <Register
                changeUser={changeUser}
                renderHeader={renderHeaderToggle}
              />
            ) : (
              <Redirect to="/" />
            )}
          </Route>
          <Route path="/product-detail/:carId" children={<ProductDetails />} />
          <Route path="/:carId/reservation">
            {user.name !== "" ? (
              <Reservation user={user} />
            ) : (
              <Redirect to="/login" />
            )}
          </Route>
          <Route path="/succes" children={<SuccesReservation />} />
        </Switch>
        <Footer />
      </BrowserRouter>
    </div>
  );
}

export default App;
