import React, { useState } from "react";
import styles from "./CardProduct.module.css";
import Button from "../Button/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import { faHeart as farFaHeart } from "@fortawesome/free-regular-svg-icons";

function CardProduct(props) {
  const [favClicked, setFavClicked] = useState(false);
  const [showMore, setShowMore] = useState(false);

  const [car, setCar] = useState({ ...props });

  // console.log("car", car);

  const handleFavChange = () => {
    if (favClicked === false) {
      setFavClicked(true);
    }
    if (favClicked === true) {
      setFavClicked(false);
    }
  };

  const toggleShowMore = () => {
    setShowMore(!showMore);
  };

  return (
    <div key={`${car.id}_${car.title}`} className={styles.container}>
      <div className={styles.image_container}>
        <FontAwesomeIcon
          icon={favClicked ? faHeart : farFaHeart}
          id={`carFav${car.id}`}
          className={styles.icon_heart}
          onClick={() => {
            car.handleFav(car.id);
            handleFavChange();
          }}
          // color={favClicked ? "red" : "#636364"}
          color={favClicked ? "red" : "#fff"}
        />

        <img src={car.img} alt={car.title} className={styles.image} />
      </div>
      <div className={styles.information}>
        <div className={styles.header}>
          <h5 className={styles.category}>{car.category}</h5>
          <h3 className={styles.title}>{car.title}</h3>
        </div>
        <div className={styles.body_information}>
          {car.features.map((feature, index) => (
            <p key={`${car.id}feature${index}`} className={styles.details}>{feature.name}</p>
          ))}
          <div>
            <p
              className={[
                styles.description,
                showMore ? styles.show_all : "",
              ].join(" ")}
            >
              {car.description}
            </p>
            {showMore ? (
              <span className={styles.btn_show_all} onClick={toggleShowMore}>
                ver menos
              </span>
            ) : (
              <span className={styles.btn_show_all} onClick={toggleShowMore}>
                mas...
              </span>
            )}
          </div>
        </div>
        <Button
          title="Ver más"
          link={`/product-detail/${car.id}`}
          btnStyle="btn--full"
          btnSize="btn--responsive"
          onClick={() => null}
        />
      </div>
    </div>
  );
}

export default CardProduct;
