import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";
import logo from "../../img/logo.png";
import Button from "../Button/Button";
import styles from "./Header.module.css";
import SocialNetworks from "../SocialNetworks/SocialNetworks";

function Header(props) {
  const [click, setClick] = useState(false);
  const [dimensions, setDimensions] = useState({
    height: "",
    width: "",
  });
  const getInitials = () => {
    const name = props.user.name;
    return name[0] + props.user.lastName[0];
  };

  const handlerClickLogout = (e) => {
    e.preventDefault();
    localStorage.removeItem("user");
    props.changeUser({ name: "", lastName: "", email:"", token: "" });
  };

  const handleClick = () => setClick(!click);

  useEffect(() => {
    setDimensions({
      height: window.innerHeight,
      width: window.innerWidth,
    });
    function handleResize() {
      setDimensions({
        width: window.innerWidth,
      });
    }
    window.addEventListener("resize", handleResize);
  }, []);

  return (
    // <div className={styles.container}>
    <div
      className={[
        styles.container,
        click && dimensions.width < 760 ? styles.active_menu : "",
      ].join(" ")}
    >
      <Link className={styles.logo_container} to="/">
        <img src={logo} className={styles.logo} alt="logo digital car" />
      </Link>
      <nav>
        <ul
          className={[
            styles.menu,
            click && dimensions.width < 760 ? styles.active : "",
          ].join(" ")}
        >
          <li className={styles.side_view}></li>
          <li className={styles.nav_title}>
            <h2>MENÚ</h2>
          </li>
          <li className={styles.nav_items}>
            {props.user.name === "" ? (
              <>
                {window.location.pathname !== "/register" ? (
                  <div className={styles.nav_item}>
                    <Button
                      title="Crear cuenta"
                      link="/register"
                      btnStyle="btn--void"
                      onClick={() => setClick(false)}
                    />
                  </div>
                ) : (
                  <></>
                )}
                {window.location.pathname !== "/login" ? (
                  <div className={styles.nav_item}>
                    <Button
                      title="Iniciar sesión"
                      link="/login"
                      btnStyle="btn--void"
                      onClick={() => setClick(false)}
                    />
                  </div>
                ) : (
                  <></>
                )}
              </>
            ) : (
              <div className={styles.panelUser}>
                <div className={styles.imgUser}>
                  <p className={styles.imgUserP}>{getInitials()}</p>
                </div>
                <p className={styles.imgUserPhrase}>
                  Hola,
                  <br />
                  <span className={styles.imgUserName}>
                    {props.user.name + " " + props.user.lastName}
                  </span>
                </p>
                <input
                  type="button"
                  value={"\u274C"}
                  className={styles.btnLogout}
                  onClick={handlerClickLogout}
                />
              </div>
            )}
            <div className={styles.social_networks}>
              <SocialNetworks snStyle="sn--style--menu" />
            </div>
          </li>
        </ul>

        <div className={styles.nav_burger} onClick={handleClick}>
          {click ? (
            <FontAwesomeIcon icon={faTimes} />
          ) : (
            <FontAwesomeIcon icon={faBars} />
          )}
        </div>
      </nav>
    </div>
  );
}

export default Header;
