import React from "react";
import "./SocialNetworks.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFacebook,
  faInstagram,
  faLinkedin,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";

function SocialNetworks({ snStyle }) {
  const STYLES = ["sn--style--footer", "sn--style--menu"];

  const SocialNetworksStyle = STYLES.includes(snStyle) ? snStyle : STYLES[0];

  return (
    <div className={`social_networks_container ${SocialNetworksStyle}`}>
      <a
        href="https://www.facebook.com"
        target="blank"
        className={`sn ${SocialNetworksStyle}`}
      >
        <FontAwesomeIcon icon={faFacebook} />
      </a>
      <a
        href="https://www.linkedin.com"
        target="blank"
        className={`sn ${SocialNetworksStyle}`}
      >
        <FontAwesomeIcon icon={faLinkedin} />
      </a>
      <a
        href="https://www.twitter.com"
        target="blank"
        className={`sn ${SocialNetworksStyle}`}
      >
        <FontAwesomeIcon icon={faTwitter} />
      </a>
      <a
        href="https://www.instagram.com"
        target="blank"
        className={`sn ${SocialNetworksStyle}`}
      >
        <FontAwesomeIcon icon={faInstagram} />
      </a>
    </div>
  );
}

export default SocialNetworks;
