import React from "react";
import {
  faCheckCircle,
  faTimesCircle,
} from "@fortawesome/free-solid-svg-icons";
import styled, { css } from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

/*---STYLES---*/
const colores = {
  borde: "#FF7E19",
  error: "#FF0000",
  exito: "#1ed12d",
};

const Label = styled.label`
  display: block;
  width: 100%;
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  cursor: pointer;
  @media (min-width: 760px) and (max-width: 1280px) {
    font-size: 14px;
  }
  /* -----DESKTOPS----- */
  @media (min-width: 1281px) {
    font-size: 14px;
  }

  ${(props) =>
    props.valido === "false" &&
    css`
      color: ${colores.error};
    `}
`;

const InputBloque = styled.div`
  position: relative;
  width: 100%;
  height: 50px;
  /* -----TABLET----- */
  @media (min-width: 760px) and (max-width: 1280px) {
    height: 50px;
  }
  /* -----DESKTOPS----- */
  @media (min-width: 1281px) {
    height: 60px;
  }
`;

const GrupoInput = styled.div`
  width: 100%;
  position: relative;
`;

const Input = styled.input`
  width: 100%;
  background: #ffffff;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  height: 30px;
  line-height: 30px;
  padding: 0 30px 0 10px;
  transition: 0.3s ease all;
  border: 3px solid transparent;
  position: relative;
  /* -----TABLET----- */
  @media (min-width: 760px) and (max-width: 1280px) {
    height: 40px;
    line-height: 40px;
    padding: 0 30px 0 10px;
  }
  /* -----DESKTOPS----- */
  @media (min-width: 1281px) {
    height: 40px;
    line-height: 40px;
    padding: 0 30px 0 10px;
  }

  &:focus {
    border: 2px solid ${colores.borde};
    outline: none;
    box-shadow: 3px 0px 30px rgba(163, 163, 163, 0.4);
  }

  ${(props) =>
    props.valido === "true" &&
    css`
      border: 3px solid transparent;
    `}

  ${(props) =>
    props.valido === "false" &&
    css`
      background: #ffe1e1;
      border: 2px solid ${colores.error} !important;
    `}
`;

const LeyendaError = styled.p`
  font-size: 12px;
  margin-bottom: 0;
  text-align: right;
  color: ${colores.error};
  display: none;
  /* -----TABLET----- */
  @media (min-width: 760px) and (max-width: 1280px) {
    margin-top: 3px;
  }
  /* -----DESKTOPS----- */
  @media (min-width: 1281px) {
    margin-top: 5px;
  }

  ${(props) =>
    props.valido === "true" &&
    css`
      display: none;
    `}

  ${(props) =>
    props.valido === "false" &&
    css`
      display: block;
    `}
`;

const IconoValidacion = styled(FontAwesomeIcon)`
  position: absolute;
  right: 5px;
  top: 7px;
  font-size: 15px;
  opacity: 0;
  @media (min-width: 760px) {
    top: 12px;
    right: 10px;
  }

  ${(props) =>
    props.valido === "false" &&
    css`
      opacity: 1;
      color: ${colores.error};
    `}

  ${(props) =>
    props.valido === "true" &&
    css`
      opacity: 1;
      color: ${colores.exito};
    `}
`;

/*----------*/

const ComponenteInput = ({
  estado,
  setEstado,
  tipo,
  label,
  placeholder,
  name,
  leyendaError,
  expresionRegular,
  funcion,
}) => {
  const onChange = (e) => {
    setEstado({ ...estado, campo: e.target.value });
  };

  const validacion = () => {
    if (expresionRegular) {
      if (expresionRegular.test(estado.campo)) {
        setEstado({ ...estado, valido: "true" });
      } else {
        setEstado({ ...estado, valido: "false" });
      }
    }

    if (funcion) {
      funcion();
    }
  };

  return (
    <InputBloque>
      <Label htmlFor={name} valido={estado.valido}>
        {label}
      </Label>
      <GrupoInput>
        <Input
          type={tipo}
          placeholder={placeholder}
          id={name}
          value={estado.campo}
          onChange={onChange}
          onKeyUp={validacion}
          onBlur={validacion}
          valido={estado.valido}
        />
        <IconoValidacion
          icon={estado.valido === "true" ? faCheckCircle : faTimesCircle}
          valido={estado.valido}
        />
      </GrupoInput>
      <LeyendaError valido={estado.valido}>{leyendaError}</LeyendaError>
    </InputBloque>
  );
};

export default ComponenteInput;
