import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle } from "@fortawesome/free-regular-svg-icons";
import ReactDatePicker from "react-datepicker";
import "./TimePicker.css";

function TimePicker(props) {
  const [startTime, setStartTime] = useState(null);

  // console.log("Start time component", startTime);

  // const { handleTimeReservation } = props.handleTimeReservation;

  return (
    <div className="container_timePicker">
      <p className="container_timePicker_title">
        <FontAwesomeIcon
          icon={faCheckCircle}
          className="checkIcon_timePicker"
        />
        Tu vehículo va a estar lista para el check-in entre las 10:00 AM y las
        11:00 PM
      </p>
      <div className="timePicker" data-testid="TimePickerTestId">
        <p className="container_timePicker_subtitle">
          Indicá tu horario estimado de retiro
        </p>
        <ReactDatePicker
          selected={startTime}
          onChange={(date) => setStartTime(date)}
          onSelect={props.handleTimeReservation(startTime)}
          placeholderText="Seleccionar hora"
          required={true}
          showTimeSelect
          showTimeSelectOnly
          timeIntervals={60}
          timeCaption="Horarios"
          dateFormat="h:mm aa"
          locale="es"
        />
      </div>
    </div>
  );
}

export default TimePicker;
