import React from "react";
import Button from "../../components/Button/Button";
import styles from "./SuccesReservation.module.css";
import succesIcon from "../../img/SuccesIcon.png";

function SuccesReservation() {
  return (
    <div className={styles.container}>
      <div className={styles.container_message}>
        <img src={succesIcon} alt="SuccesIcon" className={styles.icon} />
        <h1 className={styles.title}>¡Muchas gracias!</h1>
        <h3 className={styles.subtitle}>
          Su reserva se ha realizado con éxito
        </h3>
        <div className={styles.button}>
          <Button
            type="button"
            title="ok"
            link="/"
            btnStyle="btn--full"
            btnSize="btn--responsive"
            onClick={[]}
          />
        </div>
      </div>
    </div>
  );
}

export default SuccesReservation;
