import React, { useState, useEffect } from "react";
import ImageGallery from "react-image-gallery";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import "react-image-gallery/styles/css/image-gallery.css";
import "./Gallery.css";
import { layerGroup } from "leaflet";

function Gallery(props) {
  const [showFullScreen, setShowFullScreen] = useState(false);
  // const [images, setImages] = useState([]);

  const [images, setImages] = useState({ ...props });

  console.log("images en galeria", images);

  const galleryPhone = (
    <ImageGallery
      items={images.images.map((img) => {
        // items={images.map((img) => {
        // return { id: img.position, original: img.url, thumbnail: img.url };
        return {
          id: img.id,
          original: img.url,
          thumbnail: img.url,
        };
      })}
      showThumbnails={false}
      showPlayButton={false}
      showIndex={true}
      showFullscreenButton={false}
      slideDuration={2000}
      slideInterval={3000}
      autoPlay={true}
    />
  );

  return (
    <>
      <div className="gallery_detail_phone_and_tablet">{galleryPhone}</div>
      <div className="gallery_detail_desktop">
        {images.images.slice(0, 5).map((image) => (
          <div className={`image_container_detail_${image.position}`}>
            <img
              src={image.url}
              alt={image.url}
              className={`image_detail_${image.position}`}
            />
          </div>
        ))}

        <button
          className="gallery_btn_detail_desktop"
          onClick={() => setShowFullScreen(true)}
        >
          Ver más
        </button>
        {showFullScreen ? (
          <div className="gallery_detail_desktop_fullscreen">
            <div className="image_background_desktop_fullscreen">
              <div className="image_detail_desktop_fullscreen">
                <ImageGallery
                  items={images.images.map((img) => {
                    return {
                      id: img.id,
                      original: img.url,
                      thumbnail: img.url,
                    };
                  })}
                  showThumbnails={true}
                  showPlayButton={false}
                  showIndex={true}
                  showBullets={true}
                  showFullscreenButton={false}
                />
                <FontAwesomeIcon
                  className="close_icon_fullscreen_gallery_detail"
                  icon={faTimes}
                  onClick={() => setShowFullScreen(false)}
                />
              </div>
            </div>
          </div>
        ) : (
          <></>
        )}
      </div>
    </>
  );
}

export default Gallery;
