import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faMapMarkerAlt,
  faPlane,
  faBuilding,
  faChevronDown,
} from "@fortawesome/free-solid-svg-icons";
import styles from "./SearchCityBar.module.css";
import axios from "axios";

function SearchCityBar() {
  const [filteredData, setFilteredData] = useState([]);
  const [wordEntered, setWordEntered] = useState("");
  const [selection, setSelection] = useState("");
  const [showAll, setShowAll] = useState(false);
  const [data, setData] = useState([]);

  const handleFilter = (e) => {
    const searchWord = e.target.value;
    setWordEntered(searchWord);
    const newFilter = data.filter((value) => {
      return value.name.toLowerCase().includes(searchWord.toLowerCase());
    });
    setShowAll(false);
    if (searchWord === "") {
      setFilteredData([]);
    } else {
      setFilteredData(newFilter);
    }
  };

  const handleSelection = (e) => {
    const selectedWord = e.target.id;
    setSelection(selectedWord);
    setShowAll(false);
    document.getElementById("input_search_city").value = selectedWord;
    setFilteredData([]);
  };

  const handleShowAll = () => {
    setShowAll(!showAll);
  };

  useEffect(() => {
    axios
      .get("http://localhost:8080/cities")
      .then((response) => {
        // console.log(response.data);
        setData(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div className={styles.search} data-testid="searchCityBarTestId">
      <div className={styles.searchInputs}>
        <div className="searchIcon">
          <FontAwesomeIcon icon={faMapMarkerAlt} className={styles.mapBtn} />
          <input
            type="text"
            placeholder="Ingrese ciudad o aeropuerto..."
            autoComplete="off"
            onClick={handleShowAll}
            onChange={handleFilter}
            className={styles.input}
            id="input_search_city"
          />
          <FontAwesomeIcon
            icon={faChevronDown}
            className={styles.iconChevron}
            onClick={handleShowAll}
          />
        </div>
      </div>

      {showAll && filteredData.length === 0 ? (
        <div className={styles.dataResult}>
          {data.map((value) => {
            return (
              <div
                className={styles.options}
                key={`${value.id}_${value.country}`}
              >
                {value.type === "ciudad" ? (
                  <FontAwesomeIcon
                    icon={faBuilding}
                    className={styles.mapBtnOption}
                  />
                ) : (
                  <FontAwesomeIcon
                    icon={faPlane}
                    className={styles.mapBtnOption}
                  />
                )}
                <p
                  key={`${value.id}${value.name}`}
                  onClick={handleSelection}
                  id={value.name}
                  className={styles.firstLine}
                >
                  {value.name}
                </p>
                <p className={styles.secondLine}>{value.country}</p>
              </div>
            );
          })}
        </div>
      ) : (
        filteredData.length !== 0 && (
          <div className={styles.dataResult}>
            {filteredData.slice(0, 15).map((value) => {
              return (
                <div
                  className={styles.options}
                  key={`${value.id}_${value.country}`}
                >
                  {value.type === "ciudad" ? (
                    <FontAwesomeIcon
                      icon={faBuilding}
                      className={styles.mapBtnOption}
                    />
                  ) : (
                    <FontAwesomeIcon
                      icon={faPlane}
                      className={styles.mapBtnOption}
                    />
                  )}
                  <p
                    key={`${value.id}${value.name}`}
                    onClick={handleSelection}
                    id={value.name}
                    className={styles.firstLine}
                  >
                    {value.name}
                  </p>
                  <p className={styles.secondLine}>{value.country}</p>
                </div>
              );
            })}
          </div>
        )
      )}
    </div>
  );
}

export default SearchCityBar;
