import { React, useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import "./Calendar.css";
import DatePicker, { registerLocale } from "react-datepicker";
import es from "date-fns/locale/es";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCalendar } from "@fortawesome/free-solid-svg-icons";
import Button from "../Button/Button";

registerLocale("es", es);

function Calendar() {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [showBtn, setShowBtn] = useState(false);

  return (
    <>
      <div className="dataPicker_container" data-testid="calendarTestId">
        <div className="datePicker_one_month">
          <div className="datePicker">
            <FontAwesomeIcon icon={faCalendar} className="iconCalendar" />
            <DatePicker
              dateFormat="dd/MM/yyyy"
              minDate={new Date()}
              selected={(startDate, startDate)}
              selectsRange
              startDate={startDate}
              endDate={endDate}
              locale="es"
              monthsShown={1}
              onChange={(date) => {
                const [start, end] = date;
                setStartDate(start);
                setEndDate(end);
              }}
            />
          </div>
        </div>
        <div className="datePicker_two_month">
          <div className="datePicker">
            <FontAwesomeIcon icon={faCalendar} className="iconCalendar" />
            <DatePicker
              dateFormat="dd/MM/yyyy"
              minDate={new Date()}
              selected={(startDate, startDate)}
              selectsRange
              startDate={startDate}
              endDate={endDate}
              locale="es"
              monthsShown={2}
              shouldCloseOnSelect={false}
              onCalendarOpen={() => setShowBtn(true)}
              onCalendarClose={() => setShowBtn(false)}
              onChange={(date) => {
                const [start, end] = date;
                setStartDate(start);
                setEndDate(end);
              }}
            />
          </div>
          {showBtn ? (
            <div className="btn_apply_calendar">
              <Button
                title="Aplicar"
                link={"/"}
                btnStyle="btn--full"
                btnSize="btn--responsive"
                onClick={() => setShowBtn(false)}
              />
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    </>
  );
}

export default Calendar;
