import React from "react";
import styles from "../ProductPolicy/ProductPolicy.module.css";

function ProductPolicy() {
  return (
    <div className={styles.product_policy}>
      <p className={styles.subtitle}>¿Qué tenés que saber?</p>
      <div className={styles.container}>
        <div>
          <p className={styles.subtitle_second_type}>Normas de entrega</p>
          <ul className={styles.list}>
            <li>Tolerancia de espera de 59 minutos</li>
            <li>
              Si tu vuelo se demora o es reprogramado tenés que comunicarnos
              este cambio lo antes posible
            </li>
            <li>
              Para retirar el vehículo fuera del horario de atención tenés que
              notificarlo a la Central de Reservas
            </li>
          </ul>
        </div>
        <div>
          <p className={styles.subtitle_second_type}>Salud y seguridad</p>
          <ul className={styles.list}>
            <li>No fume dentro del vehiculo</li>
            <li>
              Se aplican las pautas de distanciamiento social y otras normas
              relacionadas con el coronavirus
            </li>
          </ul>
        </div>
        <div>
          <p className={styles.subtitle_second_type}>Política de cancelación</p>
          <ul className={styles.list}>
            <li>
              Agregá las fechas de tu viaje para obtener los detalles de
              cancelación de esta estadía
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default ProductPolicy;
